import React from 'react';
import './loading.css';
import loading from '../loading.svg'
const Loading = ({color, load, x, y}) => (
	<div  className='pt0 loading  z-max'>
	{load===true?
		<div >
	<div className=' w-100 w-50-m w-40-l h4 tc pa4 mt3 mt4-m mt0 mr-3 center-l h-50 center-m  overflow-hidden' >
			<img src={loading} height='70px' width='70px'/>
			</div>
	</div>:<p/>}
	</div>
	)
export default Loading;